@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

 * {
	box-sizing: border-box;
} 

 .body {
	background: #f6f5f7;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
    font-size: 1.0rem !important;
	font-family: -apple-system, BlinkMacSystemFont,'Moonserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue'; 
	height: 100vh;
    width: 100vw;
	margin: 0 50px;
}
.MuiInputBase-input:focus{
    background-color: #eee;
    } 

h1 {
    text-align: center;
	font-weight: bold;
	
}

h2 {
	text-align: center;
}
 
 .pp{
    font-size: 24px;
    overflow-wrap:normal;
    max-width: min-content;
        }
p { 
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}
 .search{
    padding: 7px;} 
.spanner{
    margin-top:4px}
span {
	font-size: 12px;
}

.abutton {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;}


button {
	border-radius: 20px;
	border: 2px solid #4d40c4;
	background-color: #402bff;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
    cursor: pointer;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}
.ctable1{
    border-radius: 20px;
	border: 0;
	background-color: #ff2b2b;
	color: #FFFFFF;
	font-size: 9px;
	font-weight: bold;
	padding: 6px 6px;
	letter-spacing: 0px;
    cursor: pointer;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
    }
.ctable{
    border-radius: 20px;
	border: 0;
	background-color: #3dff2b;
	color: #fffdfd;
	font-size: 9px;
	font-weight: bold;
	padding: 6px 6px;
	letter-spacing: 0px;
    cursor: pointer;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
    }
.create{
    border-radius: 15px;
	border: 2px solid #4d40c4;
	background-color: #3425b9;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 3px 5px;
    margin:10px;
	letter-spacing: 1px;
    cursor: pointer;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
    }
.survey{
    margin-top:15px ;    
    }
button:active {
	transform: scale(0.95);
}

button:focus {
	outline: none;
}

button.ghost {
	background-color: transparent;
	border-color: #FFFFFF;
}

form {
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
    
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
    width: 100%;
	text-align: center;
}

input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}
textarea{
    background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}
.container {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 100vw;
	height: 100vh;
}

.but{
    position:static;
    margin-left: 20vw;
    margin-right:20vw;
    
    
    }
 .checker {
  position: relative;
  width: 100%;
  height: 15%;
  margin: 5px;
  float: left;
  border: 2px solid #50bcf2;
  box-sizing: border-box;
  }
 
 .checker3 {
  width: 100%;
  height: 100%;
  display: flex;
  margin-bottom:15px ;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 25px;
  transition: .5s ease;
 }
 
